.FinishedQuiz {
  padding: 20px;
  color: white;
  border: 2px solid white;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 0px 10px;
}

button:hover {
  cursor: pointer;
}

ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

.success {
  color: rgba(161, 240, 69, 0.7);
}
.error {
  color: rgba(270, 97, 108, 0.7);
}