.Select {
  margin-bottom: 15px;
}

.Select label {
  margin: 0px 0px 3px 0px;
  padding: 0px;
  font-weight: 700;
  display: block;
}

.Select select {
  display: block;
  box-sizing: border-box;
  border: 1px solid #bebebe;
  margin: 0px 0px 5px;
  height: 29px;
  width: 100%;
  outline: none;
  transition: all 0.3s ease-in-out;
}